import i18n from "@/i18n";
  
  export class DateFormat {

    /**
     * Convert Date() to YYYYMMDD date format
     * @param {Date} date Date format
     * @returns string date in YYYYMMDD format
     */
    public convertDateToString(date: Date) : string {
      return date.getFullYear() + ("0" + (date.getMonth() + 1)).slice(-2) + ("0" + date.getDate()).slice(-2)
    }
    
    /**
     * Convert YYYYMMDD to DD/MM/YY date format
     * @param {string} date string in YYYYMMDD format
     * @returns string date in DD/MM/YY format
     */
    public formatStringToStringDate(date: string): string{
      if(date){
        const year = date.slice(0, 4)
        const month = date.slice(4, 6)
        const day = date.slice(6, 8)
        return `${day.toString()}/${month.toString()}/${year.toString()}`
      } else {
        return ''
      }
    }
    
    /**
     * Convert DD/MM/YYYY to YYYYMMDD date format
     * @param {string} date string in YYYYMMDD format
     * @returns string date in DD/MM/YY format
     */
    public unFormatStringToStringDate(date: string): string {
      const splitDate = date.split('/')
      return `${splitDate[2]}${splitDate[1]}${splitDate[0]}`
    }

    /**
     * Convert YYYYMMDD to Date() format
     * @param {string} date string in YYYYMMDD format
     * @returns string date in Date() format
     */
    public convertStringToDate(date: string) : Date {
      if(date){
        const year = date.slice(0, 4)
        const month = date.slice(4, 6)
        const day = date.slice(6, 8)

        const monthOption = [
          {key: "01", value: "January"},
          {key: "02", value: "Febuary"},
          {key: "03", value: "March"},
          {key: "04", value: "April"},
          {key: "05", value: "May"},
          {key: "06", value: "June"},
          {key: "07", value: "July"},
          {key: "08", value: "August"},
          {key: "09", value: "September"},
          {key: "10", value: "October"},
          {key: "11", value: "November"},
          {key: "12", value: "December"},
        ]
        
        let formattedString = ""
        
        for(let i = 0; i < monthOption.length; i++){
          if(monthOption[i].key === month ){
            formattedString = `${monthOption[i].value} ${day}, ${year} 12:00 AM`
            break;
          }
        }
        return new Date(formattedString)
      } else {
        return new Date('January 01, 2023 12:00 AM')
      }
    }

    public formatVueMonthPicker(date: {month: number, year: number}): string {
      if(date){
        const month = date.month + 1
        const year = date.year
        /* eslint-disable no-constant-condition */
        if( i18n.global.locale.value === 'en' || 'km' ){
          return `${month}/${year}`
        } else if( i18n.global.locale.value === 'zh' || 'kr' || 'jp' ){
          return `${month}/${year}`
        }
        return `${month}/${year}`
      } else {
        return ''
      }
    }

    /**
     * Convert Date() | Date()[] to DD/MM/YYYY format
     * @param {Date} date Date() | Date()[] format
     * @param {boolean} isRange optional range format
     * @returns string date in DD/MM/YYYY format or DD/MM/YYYY - DD/MM/YYYY
     */
    public formatVueDatePicker(date: Date | Date[], isRange?: boolean ): string {
      if(isRange){
        const startYear = (date[0] as Date).getFullYear()
        const startMonth = (date[0] as Date).getMonth() + 1
        const startDay = (date[0] as Date).getDate()
        const endYear = (date[1] as Date).getFullYear()
        const endMonth = (date[1] as Date).getMonth() + 1
        const endDay = (date[1] as Date).getDate()
        
        /* eslint-disable no-constant-condition */
        if( i18n.global.locale.value === 'en' || 'km' ){
          return `${startDay}/${startMonth}/${startYear} - ${endDay}/${endMonth}/${endYear}`
        } else if( i18n.global.locale.value === 'zh' || 'kr' || 'jp' ){
          return `${startDay}/${startMonth}/${startYear} - ${endDay}/${endMonth}/${endYear}`
        }

      } else {

        const day = (date as Date).getDate()
        const month = (date as Date).getMonth() + 1
        const year = (date as Date).getFullYear()

        /* eslint-disable no-constant-condition */
        if( i18n.global.locale.value === 'en' || 'km' ){
          return `${day}/${month}/${year}`
        } else if( i18n.global.locale.value === 'zh' || 'kr' || 'jp' ){
          return `${day}/${month}/${year}`
        }
        return `${day}/${month}/${year}`
      }
      return ''
    }

    public formatVueTimePicker(date: {hours: number, minutes: number}): string{

      const formatHours = date.hours < 10 ? "0" + date.hours : date.hours
      const formatMinutes = date.minutes < 10 ? "0" + date.minutes : date.minutes

      return `${formatHours}:${formatMinutes}`
    }

    public formatStringToTime(time: string ) : string {
      return time ? `${( time as string ).slice(0, 2)}:${( time as string ).slice(2, 4)}` : ''
    }

    public convertTimeStringToTimeObject ( time: string ) : TimeObject {
      const hours = time.slice(0, 2)
      const minutes = time.slice(2, 4)
      const seconds = time.slice(4, 6)
      // const milliseconds = time.slice(6, 9)

      return { "hours": Number(hours), "minutes": Number(minutes), "seconds": Number(seconds) }
    }

    public convertTimeObjectToTimeString ( time: TimeObject ) : string {
      return `${('0'+ time.hours).slice(-2)}${('0'+ time.minutes).slice(-2)}${('0'+ time.seconds).slice(-2)}000`
    }
  }

interface TimeObject {
  hours: number,
  minutes: number, 
  seconds: number
}