import { Options, Input } from './format-input.type'

export default class CardNumberFormat {
    options: Options
    input: Input
    numberRegExp: RegExp

    constructor(config: Options) {
        this.options = Object.assign(config)
        this.input = ''        
        this.numberRegExp = new RegExp('[^0-9]+', 'gi')
    }

    isNull() {
        return !this.input?.toString().replace(this.numberRegExp, '')
    }

    numbers() {
        if (typeof this.input === 'number') {
            return this.input.toString().replace('-', '')
        } else {
            return this.input?.toString().replace(this.numberRegExp, '')
        }
    }

    format(input: Input): string {
        this.input = input
        if (this.isNull()) return "";

        const { separator } = this.options
        // Limit length, prevent when user copy & paste value
        let inputValue = this.numbers()
        if(inputValue.length > 19) {
            inputValue = inputValue.substring(0, 19)
        }
        
        return inputValue.replace(/[^\dA-Z]/g, '').replace(/(.{4})/g, `$1${separator}`).trim();
    }

    unformat(input: Input): string {
        this.input = input
        if (this.isNull()) return ""
        return this.numbers()
    }
}