import { modalController } from "@ionic/vue";
import { HttpHeader } from "../type/RequestService.type";
import DialogService from "./DialogService";
import { NotificationService } from "./NotificationService";
import { Auth } from "@/auth";

const dialog = new DialogService()

export abstract class RequestErrorHandlingService {

	/**
	 * Display dialog upon request error
	 * @param {any} res server response
	 * @param {HttpHeader} httpStatus http response
	 * 
	 * Display dialog base on request or http response. 
	 */
	public static requestErrorHandler(res: any, httpStatus: HttpHeader) {

		if (!res.header.result) {
			if(httpStatus.status !== 200){
				this.errorDialogCount++
			}
            if (res.header.error_code === 'ERR000') {
                NotificationService.endNotificationInquiryInterval();
            }

			let errorContent = "";

			if (res?.header?.error_code && res?.header?.error_text) {
				errorContent = res.header.error_text;
			} else {
				errorContent = httpStatus.status ? `(${httpStatus.status}) ${httpStatus.statusText}` : "Something went wrong";
			}

			if(this.isShowDialog(httpStatus)) {
				setTimeout(async () => {
					await dialog.onOpen({
						content: errorContent,
						dialogMode: res.header.error_code,
						callback: () => {
							modalController.dismiss()
							if (res.header.error_code === 'ERR000') {
								Auth.onLogout();
							}
						}
					});
				}, 500);
			}
		} else {
			this.errorDialogCount = 0
		}
	}

	private static errorDialogCount = 0

	private static isShowDialog(httpStatus: HttpHeader) : boolean {
		if(this.errorDialogCount <= 1 && httpStatus.status !== 403 && httpStatus.status !== 401){
			return true
		} else {
			return false
		}
	}
}