import { PUM20300113_RES } from "../type/PUM20300113.type";
import { DataCenter } from "./DataCenter";
import { Auth } from "@/auth";

export abstract class NotificationService {

    private static intervalID: number | undefined

    public static async startNotificationInquiryInterval() {
        
        this.intervalID  = setInterval(async () => {
            if(await DataCenter.getData('login_session_id')){

                const res = await this.getUnreadNotification()
                
                if(res.header.result){
                
                    console.log('Notification Inquiry Interval Executed');
                
                    await DataCenter.setData('notification_count', res.body.unReadMessageTotalCount)

                    // dispatch event for vue to listen 
                    window.dispatchEvent(new CustomEvent('notification_update', {
                        detail: {
                            notification_count: res.body.unReadMessageTotalCount
                        }
                    }))
                }
            }
        }, 60000)
    }

    public static async endNotificationInquiryInterval() {
        clearInterval(this.intervalID)
        console.log('Notification Inquiry Interval Ended');
    }

    //PUM20300113_Adapter_NotificationInfoUnReadMessage_TotalCount_Inquiry
    private static async getUnreadNotification(): Promise<PUM20300113_RES> {
        //check for session timeout
        if(await Auth.checkSessionTimeout()){
            Auth.limitApiRequest++
            this.endNotificationInquiryInterval()
            Auth.handleSessionEnd()
        }

        return await new Promise((resolve) => {
            window.bizMOB.Network.requestTr({
                _bMock: false,
                _sTrcode: 'PUM20300113',
                _oBody: {},
                _fCallback: (res: PUM20300113_RES) => {

                    //notification doesn't show error dialog

                    resolve(Object.assign({}, res));
                }
            });
        });
    }
}