export type Input = number | string
export type InputType = 'account-number' | 'phone-number' | 'currency' | 'card-number' | 'cvv' | 'expiry-date'

export interface Options {
    inputType : InputType
    clearable : boolean
    isReturnFormatValue : boolean 
    modelValue : string
    separator: string
    prefill : boolean
    prefix: string
    suffix: string
    decimal: string
    fractionDigits: number
}

export interface CustomInputElement extends HTMLInputElement {
    options: Options
    formatValue?: Input
    unformatValue?: Input
    oldValue?: Input
    cleanup: () => void
}

export interface InputCustomStyle {
    style: {
        [key: string]: string;
    }
}

export default {
    inputType : '',
    clearable : false,
    isReturnFormatValue : false, 
    modelValue : '',
    separator: '',
    prefill : true,
    prefix: '',
    suffix: '',
    decimal: '.',
    fractionDigits: 2
}