import { loadingService } from "@/shared/services/LoadingScreenService";
import { ApprovalService } from "./ApprovalService";
import { Auth } from "@/auth";
import { APM20000111_RES, AuthHeader, HttpHeader } from "../type/RequestService.type";
import router from "@/router";
import { RequestErrorHandlingService } from "./RequestErrorHandlingService";

const loading = new loadingService();
const approval = new ApprovalService();

/**
 * Request Service 
 * @param trCode (string) request code
 * @param isMock (boolean) (Default: False)if true, get data from public/mock
 * @param body (object) request body
 * @param authorization (object)
 * @param tabId (string) (Optional)
 * @returns {Promise<any>} a promise of the requested data
 * 
 * General service for requesting api
 */
export class RequestService {

  public async getRequest(trCode: string, body: object, isMock?: boolean, tabId?: string, isDisplayLoading = true, isShowDialog = true, authorization?: AuthHeader, finalAuthorizerYN?: 'Y' | 'N') {
    
    if(Auth.limitApiRequest == 0){
      if(trCode !== 'LOGIN' && trCode !== 'SEC20100112' && trCode !== 'SEC20100121'){
        if(await Auth.checkSessionTimeout()){
          Auth.limitApiRequest++
          Auth.handleSessionEnd()
        } else {
          return await this.checkApprovalLine(trCode, body, isMock, tabId, isDisplayLoading, isShowDialog, authorization, finalAuthorizerYN)
        }
      } else {

        if(isDisplayLoading === true){
          loading.openloading()
        }
  
        return new Promise((resolve) => {
          window.bizMOB.Network.requestTr({
            _bMock: isMock || false,
            _sTrcode: trCode,
            _oBody: {
              ...body,
            },
            _fCallback: (res: any, httpStatus: HttpHeader) => {
              if(isDisplayLoading === true){
                loading.closeLoading()
              }
              // error handling
              RequestErrorHandlingService.requestErrorHandler(res, httpStatus)

              Auth.initSession()
              resolve(Object.assign({}, res));
            },
          });
        });
      }
    }
  }

  private async checkApprovalLine(trCode: string, body: object, isMock?: boolean, tabId?: string, isDisplayLoading?: boolean, isShowDialog?: boolean, authorization?: AuthHeader, finalAuthorizerYN?: 'Y' | 'N'){
    const approvalLine = await approval.checkApprovalLine(trCode, isMock, finalAuthorizerYN) as APM20000111_RES    
    
    // No approval line
    if(approvalLine.body){
      if( approvalLine.body.requiredAuthoriseLineYN === 'N' || router.currentRoute.value.query.approvalType){
      
        if(isDisplayLoading === true){
          loading.openloading()
        }
  
        return new Promise((resolve) => {
          
          window.bizMOB.Network.requestTr({
            _bMock: isMock || false,
            _sTrcode: trCode,
            _oApproval: {
              requester_id: authorization ? authorization.requester_id : '',
              approver_id: authorization ? authorization.approver_id : '',
              authorization_no: authorization ? authorization.authorization_no : 0,
              authorization_seq: authorization ? authorization.authorization_seq : 0,
              authorization_status_code: authorization ? authorization.authorization_status_code : '',
              authorization_remark: authorization ? authorization.authorization_remark : '',
            },
            _oBody: {
              ...body,
            },
            _fCallback: (res: any, httpStatus: HttpHeader) => {
              if(isDisplayLoading === true){
                loading.closeLoading()
              }
              // error handling
              if(isShowDialog){
                RequestErrorHandlingService.requestErrorHandler(res, httpStatus)
              }

              Auth.initSession()
              resolve(Object.assign({}, res));
            },
          });
        });
      } else {
        return await approval.showApprovalPopup(approvalLine, body, trCode, tabId)
      }
    } else {
      return {header: {result: false}}
    }
  }
}