import type { Dialog } from '@/shared/type/dialog.type';
import i18n from "@/i18n";
import { modalController } from '@ionic/vue';
import baseDialog from '@/shared/components/BaseDialog/BaseDialog.vue'

/**
   * Dialog 
   * @param title (string) dialog title
   * @param content (string|component) dialog content/description
   * @param btnLabel (string)
   * @param showCancel (boolean)
   * @param dialogMode (string)
   * @param callback
*/
export class DialogService {
[x: string]: any;
    private translate = i18n.global.t

    async onOpen({ title, content, btnLabel = this.translate('common.yes'), showCancel, dialogMode, callback }: Dialog) {
        setTimeout(async() => {
            const modal = await modalController.create({
                component: baseDialog,
                cssClass: "",
                componentProps: {
                    title,
                    content,
                    btnLabel,
                    showCancel,
                    dialogMode
                },
            });
            await modal.present();
    
            const { role, data } = await modal.onWillDismiss();
            if (role === "ok") {
                callback(data)
            }
        }, 200);
    }
}

export default DialogService