import { RequestService } from "./shared/services/RequestService";
import { SEC20100112_REQ_BODY, SEC20100112_RES } from "./shared/type/SEC20100112.type";
import router from "./router";
import { SEC20100121_REQ_BODY, SEC20100121_RES } from "./shared/type/SEC20100121.type";
import { SEC20100111_REQ_BODY } from "./shared/type/SEC20100111.type";
import { LOGIN_RES } from "./shared/type/LOGIN.type";
import { DataCenter } from "./shared/services/DataCenter";
import PopupService from "./shared/services/PopupService";
import SessionEndPopup from "./shared/components/SessionEndPopup/SessionEndPopup.vue";
import { NotificationService } from "./shared/services/NotificationService";

const request = new RequestService()
export class Auth {

  public getEnvMode() : 'production' | 'development' | 'sit' | 'uat' {
    return process.env.NODE_ENV
  }

  public async onLogin(reqBody : SEC20100112_REQ_BODY) : Promise<SEC20100112_RES> {
      // SEC20100112_Adapter_EmployeeInfo_Login_Prechecking
      return await request.getRequest('SEC20100112', reqBody, false) as SEC20100112_RES
  }

  public async onSendOTP(reqBody : SEC20100121_REQ_BODY) : Promise<SEC20100121_RES> {
    return await request.getRequest('SEC20100121', reqBody, false) as SEC20100121_RES
  }

  public async onVerifyOtp(otp: SEC20100111_REQ_BODY) {
    const reqBody = {
      os_type: "",
      device_id: "",
      app_key: "",
      user_id: "",
      emulator_flag: false,
      manual_phone_number: true,
      auth_no: "unknown",
      legacy_message: {
          header: {
              channel_type_code: "01",
              error_code: "",
              error_text: "",
              id_token: "",
              info_text: "",
              login_session_id: "",
              message_version: "",
              result: true,
              screen_id: "",
              timestamps: "1628493235815",
              trcode: "SEC20100111",
              uuid: "abc"
          },
          body: { ...otp }
      },
      legacy_trcode: "SEC20100111"
    }

    // SEC20100111_Adapter_EmployeeInfo_Login
    const res = await request.getRequest('LOGIN', reqBody, false) as LOGIN_RES

    if(res.body.legacy_message.header.result) {
      await DataCenter.setData('isAuthenticated', 'true')
      await DataCenter.setData('branchCode', res.body.legacy_message.body.branchCode)
      await DataCenter.setData('branchName', res.body.legacy_message.body.branchCode)
      await DataCenter.setData('employeeNo', res.body.legacy_message.body.employeeNo)
      await DataCenter.setData('employeeName', res.body.legacy_message.body.employeeName)
      await DataCenter.setData('login_session_id', res.body.legacy_message.header.login_session_id)

      router.push('/COM200001SC00')
    }
  }

  public static async onLogout() {
    await DataCenter.clearData()
    router.push('/security/sign-in')
  }

  //Tracking Session Timeout____________________________________________________________
  public static async initSession() {
    const currentDatetime = new Date().toISOString();
    DataCenter.setData('storedDatetime', currentDatetime)
  }

  public static limitApiRequest = 0

  public static async checkSessionTimeout() : Promise<boolean> {
    const sessionDuration = Number(process.env.VUE_APP_SESSION_DURATION) //min

    const storedDatetime = new Date(await DataCenter.getData('storedDatetime'));
    const currentDatetime = new Date();

    const diffInMinutes = (currentDatetime.getTime() - storedDatetime.getTime()) / (1000 * 60);

    return diffInMinutes > sessionDuration
  }

  public static handleSessionEnd() {

    const popupService = new PopupService()

    NotificationService.endNotificationInquiryInterval()

    popupService.onOpen({
      dataProp: {},
      component: SessionEndPopup,
      callback: ()=>{
        Auth.onLogout()
      }
    })
  }
}