
import { PropType, defineComponent } from 'vue'
import { modalController } from '@ionic/vue';

export default defineComponent({
    name: "baseDialogVue",
    props: {
        title: {
            type: String
        },
        content: {
            type: String
        },
        btnLabel: {
            type: String
        },
        showCancel: {
            type: Boolean,
            default: false
        },
        dialogMode: {
            type: String as PropType<'Success' | 'Error' | 'Warning' | "Question">
        }
    },
    data() {
        return {}
    },

    computed:{
        diaglogTitle(){
            switch (this.dialogMode) {
                case 'Success':
                    return 'Success!'
                case 'Error':
                    return 'Error!'
                case 'Warning':
                    return 'Warning!'
                case 'Question':
                    return 'Question?'
                default:
                    return ''
            }
        }
    },

    methods: {
        handleClose() {
            return modalController.dismiss()
        },
        onClickOK() {
            return modalController.dismiss(null, 'ok')
        }
    }
})
