
import { PropType, defineComponent } from 'vue';
import { modalController } from '@ionic/vue'
import { EmployeeByRoleID } from '@/shared/type/COM21200114.type';
import { COM20700214_REQ_BODY, COM20700214_RES } from '@/shared/type/COM20700214.type';
import { APM20000111_RES, HttpHeader } from '@/shared/type/RequestService.type';
import { Auth } from '@/auth';
import { DataCenter } from '@/shared/services/DataCenter';
import { APM20200121_REQ_BODY, APM20200121_RES, AuthorizationLineList } from '@/shared/type/APM20200121.type';
import { RequestErrorHandlingService } from '@/shared/services/RequestErrorHandlingService';

export default defineComponent({
    name: "apm200001sp01",
    props: {
        approvalList: {
            type: [] as PropType<APM20000111_RES>,
            default: [],
            required: true
        },
        payload: {
            type: Object as PropType<object>,
            required: true
        },
        trCode: {
            type: String,
            required: true
        },
        tabId: {
            type: String,
            required: false
        },
    },

    data() {
        return {
            mappedList: [] as AuthorizationLineListDisplay[],
            servicePayload: {} as any,
            userRoles: [] as any[],
            memoContent: '',
            invalidAuthList: [] as number[],
            isInvalid: false,
            enableApprover: '' as string,
            isDisabledApprover: false as boolean,
            headers: [
                'Authorization Step',
                'Branch/Dept Name',
                'Authorizer Role',
                'Mandatory Approval',
                'Enable Approver',
                'Authorizer Name / Staff ID',
                'Authorizer Phone No'
            ]
        }
    },

    async mounted() {
        this.mappedList = await this.mapRoleToStaff()
        this.servicePayload = this.payload
    },
    methods: {
        async mapRoleToStaff(): Promise<AuthorizationLineListDisplay[]> {
            let mappedList = [] as AuthorizationLineListDisplay[]
            const list = this.approvalList.body.list

            for (let index = 0; index < list.length; index++) {

                await this.getUserRole(list[index].branchCode, list[index].branchTypeCode, list[index].authorizationRoleCode).then((res: any) => {
                    const employeeToChoose = [] as EmployeeByRoleID[]
                    res.body.list.forEach((element: EmployeeByRoleID) => {
                        employeeToChoose.push(element)
                    });
                    this.userRoles.push(employeeToChoose)
                })

                mappedList.push({
                    // for display,
                    // branchName: '',
                    // authorizerRole: '',
                    authorizerPhoneNo: '',
                    branchName: `${list[index].branchName} (${list[index].branchCode})`,
                    branchCode: `${list[index].branchCode}`, 
                    authorizerRole: `${list[index].authorizationRoleCode}: ${list[index].authorizationRoleName}`,
                    // authorizerPhoneNo: `${this.userRoles[0][0] ? this.userRoles[0][0].cellPhoneNo : ''}`,
                    // for register
                    seqNo: list[index].seqNo,
                    authorizationBizTypeCode1: list[index].authorizationBizTypeCode1,
                    authorizationBizTypeCode2: list[index].authorizationBizTypeCode2,
                    authorizationBranchCode: list[index].branchCode,
                    authorizationBranchTypeCode: list[index].branchTypeCode,
                    authorizationEmpName: '',
                    authorizationEmployeeNo: '',
                    // authorizationEmpName: `${this.userRoles[0][0] ? this.userRoles[0][0].userName : ''}`,
                    // authorizationEmployeeNo: `${this.userRoles[0][0] ? this.userRoles[0][0].userID : ''}`,
                    authorizationRoleCode: list[index].authorizationRoleCode,
                    mandatoryApprovalYN: list[index].mandatoryApprovalYN,
                    isEnableApprover: list[index].mandatoryApprovalYN == 'Y' ? true : false,
                })
            }

            return mappedList
        },

        async getUserRole(branchCode: string, branchTypeCode: string, userRoleID: string): Promise<COM20700214_RES | undefined> {

            const reqBody: COM20700214_REQ_BODY = {
                //if line is setup for branch, filter by branch('01')
                //if line is setup for dept, filter by all('')
                branchCode: branchTypeCode == '01' ? branchCode : '',
                userRoleID: userRoleID,
                employeeNo: await DataCenter.getData('employeeNo') as string || ''
            }

            if (await Auth.checkSessionTimeout()) {
                Auth.limitApiRequest++
                Auth.handleSessionEnd()
            } else {
                return await this.$request.getRequest('COM20700214', reqBody, false) as COM20700214_RES
            }
        },

        handleChangeAuthorizer(key: string) {
            exit_loops:
            for (let i = 0; i < this.userRoles[key].length; i++) {
                for (let j = 0; j < this.userRoles[key].length; j++) {
                    if (this.userRoles[key][j].userName === this.mappedList[key].authorizationEmpName) {
                        this.mappedList[key].branchName = this.userRoles[key][j].branchName
                        this.mappedList[key].branchCode = this.userRoles[key][j].branchCode
                        this.mappedList[key].authorizationBranchCode = this.userRoles[key][j].branchCode
                        this.mappedList[key].authorizerRole = this.approvalList.body.list[key].authorizationRoleName
                        this.mappedList[key].authorizerPhoneNo = this.userRoles[key][j].cellPhoneNo
                        this.mappedList[key].authorizationEmployeeNo = this.userRoles[key][j].userID
                        break exit_loops;
                    }
                }
            }
        },

        async onRequestApproval() {
            this.isInvalid = false
            if (this.memoContent !== '') {
                const reqBody: APM20200121_REQ_BODY = {
                    authorizationLineList: this.handlePrepareLineList(),
                    linkScreenNo: `${this.$route.params.dynamic}${this.tabId ? this.tabId : ''}`,
                    payload: JSON.stringify(this.payload),
                    serviceID: this.trCode,
                    employeeNo: await DataCenter.getData('employeeNo') as string, // For Testing Purpose Only
                    memoContents: `${this.approvalList.body?.managerApprovalDescription} ${this.memoContent}`,

                }
                // validate auth line
                if (this.invalidAuthList.length === 0) {

                    const requester_id = await DataCenter.getData('employeeNo') as string // For Testing Purpose Only

                    if (await Auth.checkSessionTimeout()) {
                        Auth.limitApiRequest++
                        Auth.handleSessionEnd()
                    } else {
                        return new Promise((resolve) => {
                            window.bizMOB.Network.requestTr({
                                // _bMock: process.env.NODE_ENV != 'development' ? false : IS_MOCK_APPROVAL_LINE,
                                _sTrcode: "APM20200121",
                                _oApproval: {
                                    requester_id: requester_id,
                                    approver_id: '',
                                    authorization_no: 0,
                                    authorization_seq: 0,
                                    authorization_status_code: '',
                                    authorization_remark: '',
                                },
                                _oBody: {
                                    ...reqBody
                                },
                                _fCallback: (res: APM20200121_RES, httpStatus: HttpHeader) => {
                                    // error handling
                                    RequestErrorHandlingService.requestErrorHandler(res, httpStatus)
                                    Auth.initSession()
                                    resolve(Object.assign({}, res));
                                }
                            });
                        }).then(async (res: any) => {
                            return await modalController.dismiss(res, 'confirm')
                        })
                    }
                }
            } else {
                this.isInvalid = true
            }
        },

        handlePrepareLineList(): AuthorizationLineList[] {
            const list = [] as AuthorizationLineList[]
            this.invalidAuthList = []

            this.mappedList.forEach((v, index) => {
                list.push({
                    authorizationBizTypeCode1: v.authorizationBizTypeCode1,
                    authorizationBizTypeCode2: v.authorizationBizTypeCode2,
                    authorizationBranchCode: v.authorizationBranchCode,
                    authorizationBranchTypeCode: v.authorizationBranchTypeCode,
                    authorizationEmpName: v.isEnableApprover ? v.authorizationEmpName : '',
                    authorizationEmployeeNo: v.isEnableApprover ? v.authorizationEmployeeNo : '',
                    authorizationRoleCode: v.authorizationRoleCode,
                    seqNo: v.seqNo,
                    mandatoryApprovalYN: v.mandatoryApprovalYN,
                    enableApproverYN: v.isEnableApprover ? 'Y' : 'N'
                })

                if (v.authorizationEmployeeNo == '' && v.isEnableApprover) {
                    this.invalidAuthList.push(index)
                }
            })

            return list
        },

        handleFormatPhoneNumber(phoneNumber: string) {
            return phoneNumber ? this.$format.formatPhoneNumber(phoneNumber) : ''
        },

        handleDisableStaffFromSelect(staffId: string): boolean {
            let isDisabled = false
            for (let i = 0; i < this.mappedList.length; i++) {
                if (this.mappedList[i].authorizationEmployeeNo == staffId) {
                    isDisabled = true
                }
            }
            return isDisabled
        },

        onClose() {
            return modalController.dismiss({
                header: {
                    result: false
                }
            }, 'confirm')
        },
    }
})

interface AuthorizationLineListDisplay extends AuthorizationLineList {
    authorizerPhoneNo: string,
    branchName: string,
    branchCode: string,
    authorizerRole: string
    isEnableApprover?: boolean,
    enableApproverYN?: string
}
