import LoggerService from '@/shared/bizMOB/LoggerService';
import { VNode } from 'vue'
import AccountFormat from './account-format'
import CurrencyFormat from './currency-format'
import PhoneFormat from './phone-format'
import CardNumberFormat from './card-number-format';
import ExpiryDateFormat from './expiry-date-format';
import { CustomInputElement, Options } from './format-input.type'

const loggerService = new LoggerService()

export const MINUS = '-'

export class CustomInputEvent<T = any> extends CustomEvent<T> {
    target!: CustomInputElement
}

export function cloneDeep(data: object) {
    return JSON.parse(JSON.stringify(data))
}

export function FacadeEvent(event: string) {
    return new CustomEvent(event, {
        bubbles: true,
        cancelable: true,
        detail: { facade: true }
    })
}

export function getInputElement(el: HTMLElement | HTMLInputElement): CustomInputElement {
    const inputElement = el instanceof HTMLInputElement ? el : el.querySelector<HTMLInputElement>('input')

    if (!inputElement) {
        throw new Error('facade directive requires an input element')
    }

    return inputElement as CustomInputElement
}

export function updateCursor(el: HTMLInputElement, position: number) {
    const setSelectionRange = (): any => {
        el.setSelectionRange(position, position)
    }
    setSelectionRange()
    setTimeout(setSelectionRange(), 1)
}

export function updateValue(el: CustomInputElement, vnode: VNode | null, { emit = true, force = false, blur = false } = {}) {
    
    const { options, oldValue } = el
    const { inputType, decimal, fractionDigits } = options
    let currentValue = vnode?.props?.value || el.value
    if (typeof currentValue !== 'string') {
        currentValue = currentValue.toString()
    }

    if (force || oldValue !== currentValue) {
        let format = ''
        let unformat = ''

        if(inputType === 'currency') {
            const number = new CurrencyFormat(options).blur(blur)
            const parts = currentValue.split('.')
            // To remove the last char when value length after decimal is more that fractionDigits
            if (currentValue.includes(decimal) && parts[1].length > fractionDigits) {
                currentValue = parts[0] + decimal + parts[1].slice(0, -1);
            }
            format = number.format(currentValue)
            unformat = number.blur().unformat(currentValue)
        } else if (inputType === 'phone-number') {
            const number = new PhoneFormat(options)
            format = number.format(currentValue)
            unformat = number.unformat(currentValue)
        } else if (inputType === 'account-number') {
            const number = new AccountFormat(options)
            format = number.format(currentValue)
            unformat = number.unformat(currentValue)
        } else if (inputType === 'card-number') {
            const number = new CardNumberFormat(options)
            format = number.format(currentValue)
            unformat = number.unformat(currentValue)
        } else if (inputType === 'expiry-date') {
            const number = new ExpiryDateFormat(options)
            format = number.format(currentValue)
            unformat = currentValue
        } else if (inputType === 'cvv') {
            format = currentValue.substring(0,3)
            unformat = currentValue.substring(0,3)
        } else {
            loggerService.warn('Invalid input type, inputType should be account-number, phone-number or currency')
        }

        el.oldValue = format
        el.formatValue = format
        el.unformatValue = unformat

        if (el.value !== format) {
            el.value = format
        }

        return emit && el.dispatchEvent(FacadeEvent('input'))
    }
}

export function inputHandler(event: CustomInputEvent) {
    const { target, detail } = event

    if (detail?.facade) {
        return false
    }

    const { oldValue, options, formatValue } = target
    const {inputType, decimal, fractionDigits } = options as Options

    event.stopPropagation()

    let positionFromEnd = target.value.length
    if (target.selectionEnd) {
        positionFromEnd = target.value.length - target.selectionEnd
    }

    updateValue(target, null, { emit: false })

    if (options.suffix) {
        positionFromEnd = Math.max(positionFromEnd, options.suffix.length)
    }

    positionFromEnd = target.value.length - positionFromEnd

    if (options.prefix) {
        positionFromEnd = Math.max(positionFromEnd, options.prefix.length)
    }
    // Update cursor + 1 when cursor is at part decimal
    if (inputType === 'currency' && target.value.includes(decimal) && positionFromEnd > target.value.length - (fractionDigits + 1)) {
        positionFromEnd += 1
    }

    updateCursor(target, positionFromEnd)

    if (oldValue !== target.value) {
        target.oldValue = formatValue
        target.dispatchEvent(FacadeEvent('input'))
    }
}


export function blurHandler(event: Event) {
    const { target, detail } = event as CustomInputEvent

    if (detail?.facade) {
        return false
    }

    const { oldValue, formatValue } = target

    updateValue(target, null, { emit: false, force: true, blur: true })

    if (oldValue !== target.value) {
        target.oldValue = formatValue
        target.dispatchEvent(FacadeEvent('change'))
    }
}

export function keydownHandler(event: KeyboardEvent, el: CustomInputElement) {
    const { options } = el
    const { prefix, separator, inputType, suffix, decimal, fractionDigits } = options as Options
    const { key } = event
    const numRegExp = new RegExp('[^0-9]+', 'gi')
    const valueNumOnly = el?.value?.toString().replace(numRegExp, '')
    const selectionEnd = el.selectionEnd || 0
    const selectionStart = el.selectionStart || 0
    const character = el.value.slice(selectionEnd - 1, selectionEnd)
    const replace = el.value.slice(selectionEnd - 2, selectionEnd)
    let positionFromEnd = el.value.length - selectionEnd
    
    if (key === 'Backspace') {
        // check current cursor position is after separator when backspace key down
        if ([prefix, MINUS, separator].includes(character)) {
            event.preventDefault()
            if (character === separator) {
                el.value = el.value.replace(replace, '')
            } else {
                el.value = el.value.replace(new RegExp(`[${prefix}${MINUS}]`, 'g'), '')
            }
            positionFromEnd = Math.max(positionFromEnd, suffix.length)
            positionFromEnd = el.value.length - positionFromEnd
            positionFromEnd = Math.max(positionFromEnd, prefix.length)
            updateCursor(el, positionFromEnd)
            el.dispatchEvent(new Event('input'))
        }
    } 
    if (key !== 'Backspace' && key !== 'ArrowLeft' && key !== 'ArrowRight') {
        if (inputType === 'currency') {
            const regExp = new RegExp(`${prefix}|${suffix}`, 'g')
            const newValue = el.value.replace(regExp, '')
    
            if (positionFromEnd === 0 && (newValue.indexOf(decimal) !== -1) && (newValue.substring(newValue.indexOf(decimal)).length > fractionDigits) && (selectionStart >= newValue.length) && key !== MINUS) {
                event.preventDefault();
            }
        } else if (inputType === 'card-number' && valueNumOnly.length >= 19 && (selectionStart > valueNumOnly.length)) {
            event.preventDefault()
        } else if (inputType === 'account-number' && valueNumOnly.length >= 16 && (selectionStart > valueNumOnly.length)) {
            event.preventDefault()
        } else if (inputType === 'expiry-date' && valueNumOnly.length >= 4 && (selectionStart > valueNumOnly.length)) {
            event.preventDefault()
        } else if (inputType === 'cvv' && valueNumOnly.length >= 3 && (selectionStart > valueNumOnly.length)) {
            event.preventDefault()
        } else if (inputType === 'phone-number' && el.value.length >= 12 && (selectionStart > valueNumOnly.length)) {
            // Stop input value
            if (String(el.value).slice(0, 3) === '855') {
                if (valueNumOnly.length >= 12) {
                    event.preventDefault()
                }
            } else {
                if (valueNumOnly.length >= 10) {
                    event.preventDefault()
                }
            }
        }
    }
    
}
