import { modalController } from "@ionic/vue";
import { DefaultPopup } from "../type/popup.type";
import { markRaw } from "vue";

class PopupService {
    async onOpen({ dataProp, component, callback }: DefaultPopup) {
        const popup = await modalController.create({
            component: markRaw(component),
            cssClass: "",
            componentProps: dataProp
        });

        await popup.present();

        const { role, data } = await popup.onWillDismiss();
        if (role === "confirm" && callback) {
            callback(data)
        }
    }
}

export default PopupService
