export class loadingService {
  /**
   * Loading Service 
   * @param duration (string)
   */
  private className;
  private openLoadingNum = 0
  private closeLoadingNum = 0
  
  constructor() {
    this.className = document.getElementById("loading-service")?.classList;
  }

  public openloading() {
    this.openLoadingNum++
    this.className.remove("loading-hide");
  }
  public closeLoading() {
    this.closeLoadingNum++
    setTimeout(() => {
      if(this.openLoadingNum === this.closeLoadingNum){
        this.openLoadingNum = 0
        this.closeLoadingNum = 0
        this.className.add("loading-hide");
      }
    }, 500);
  }
  public onLoadingWithDuration(duration: number) {
    this.className.remove("loading-hide");
    setTimeout(() => {
      this.className.add("loading-hide");
    }, duration);
  }
}
