
import { Auth } from '@/auth';
import { DataCenter } from '@/shared/services/DataCenter';
import { loadingService } from '@/shared/services/LoadingScreenService';
import { RequestErrorHandlingService } from '@/shared/services/RequestErrorHandlingService';
import { APM20200132_REQ_BODY, APM20200132_RES } from '@/shared/type/APM20200132.type';
import { APM20200133_REQ_BODY, APM20200133_RES } from '@/shared/type/APM20200133.type';
import { HttpHeader } from '@/shared/type/RequestService.type';
import { modalController } from '@ionic/vue';
import { PropType, defineComponent } from 'vue';

const loading = new loadingService()

enum AuthorizationStatusCode {
	request = '01',
	approving = '02',
	final = '03',
	return = '04',
	cancel = '05'
}

export default defineComponent({
	name: "ApprovalReason",
	props: {
		reason: {
			type: String,
		},
		payload: {
			type: Object as PropType<Payload>,
			required: true
		}
	},
	data() {
		return {
			content: '',
			operation: {
				allowCancel: false,
				allowApprove: false
			},
			isInValidInput: false
		}
	},
	async mounted() {
		this.content = this.reason ? this.reason : ''
		await this.checkAllowOperation()
	},
	computed: {
		isEnable() : boolean {
			enum searchTypeCode {
				needApproval = '01'
			}
			enum authCode {
				final = '03',
				request = '01',
				processing = '02'
			}
			if(this.$route.query.approvalType === 'my-approval' && this.$route.query.searchTypeCode === searchTypeCode.needApproval){
				return this.$route.query.approvalStatus === authCode.request || this.$route.query.approvalStatus === authCode.processing
			} else {
				return false
			}
		},
	},
	methods: {
		onClickButton(button: 'return' | 'decline' | 'cancel'){
			switch(button){
				case 'return':
					this.isInValidInput = false
					if(this.content){
						this.$dialog.onOpen({
							content: 'Do you want to return this request?',
							dialogMode: 'Question',
							callback: async () => {
								await this.onReturn()
								await modalController.dismiss()
							}
						})
					} else {
						this.isInValidInput = true
					}
				break;
				case 'decline':
					this.isInValidInput = false
					if(this.content){
						this.$dialog.onOpen({
							content: 'Do you want to decline this request?',
							dialogMode: 'Question',
							callback: async () => {
								await this.onDecline('decline')
								await modalController.dismiss()
							}
						})
					} else {
						this.isInValidInput = true
					}
				break;
				case 'cancel':
					this.$dialog.onOpen({
						content: 'Do you want to cancel this request?',
						dialogMode: 'Question',
						callback: async () => {
							await this.onDecline('cancel')
							await modalController.dismiss()
						}
					})
				break;
			}
		},
		async onApprove(){
			this.isInValidInput = false
			const res = await this.handleApprove()

			if (res.header.result) {
				this.$dialog.onOpen({
					content: 'Request Approved',
					dialogMode: 'Success',
					callback: () => {
						modalController.dismiss()
						this.$router.back()
						this.onCancel()
					}
				});
			}
		},
		async onReturn(){
			const reqBody : APM20200133_REQ_BODY  = {
				authorizationNo : this.payload.authorizationNo,
				authorizerEmployeeNo: await DataCenter.getData('employeeNo') || '',
				memoContents : this.content,
				seqNo : this.payload.currentEmpApprovingSeqNo
			};
			const authorizationBody = {
				requester_id: this.payload.requesterID,
				approver_id: localStorage.getItem('employeeNo') || '',
				authorization_no: this.payload.authorizationNo ? this.payload.authorizationNo : 0,
				authorization_seq: this.payload.currentEmpApprovingSeqNo ? this.payload.currentEmpApprovingSeqNo : 0,
				authorization_status_code: this.payload.finalAuthorizerYN === 'Y' ? '03' : '02',
				authorization_remark: this.content ? this.content : ''
			}
			const res = await this.$request.getRequest('APM20200133', reqBody, false, undefined, true, true, authorizationBody, this.payload.finalAuthorizerYN) as APM20200133_RES
			if (res.header.result) {
				this.$dialog.onOpen({
					content: 'Request Returned',
					dialogMode: 'Success',
					callback: () => {
						modalController.dismiss()
						this.onCancel()
					}
				});
			}
		},
		async onDecline(mode: 'decline' | 'cancel'){
			const reqBody : APM20200132_REQ_BODY  = {
				authorizationNo : this.payload.authorizationNo,
				authorizerEmployeeNo: await DataCenter.getData('employeeNo') || '',
				memoContents : mode == 'decline' ? this.content : 'Request has been canceled by the requester.',
				seqNo : this.payload.currentEmpApprovingSeqNo
			};
			const authorizationBody = {
				requester_id: this.payload.requesterID,
				approver_id: localStorage.getItem('employeeNo') || '',
				authorization_no: this.payload.authorizationNo ? this.payload.authorizationNo : 0,
				authorization_seq: this.payload.currentEmpApprovingSeqNo ? this.payload.currentEmpApprovingSeqNo : 0,
				authorization_status_code: mode == 'cancel' ? AuthorizationStatusCode.cancel : this.payload.finalAuthorizerYN === 'Y' ? AuthorizationStatusCode.final : AuthorizationStatusCode.approving,
				authorization_remark: mode == 'decline' ? this.content ? this.content : '' : 'Request has been canceled by the requester.'
			}
			const res = await this.$request.getRequest('APM20200132', reqBody, false, undefined, true, true, authorizationBody, this.payload.finalAuthorizerYN) as APM20200132_RES
			if (res.header.result) {
				this.$dialog.onOpen({
					content: 'Request Declined',
					dialogMode: 'Success',
					callback: () => {
						modalController.dismiss()
						this.onCancel()
					}
				});
			}
		},
		async handleApprove() : Promise<any>{
			enum authCode {
				final = '03',
				request = '01',
				processing = '02'
			}
			const reqBody = {
				...JSON.parse(this.payload.requestContent)
			}
			const authorizationBody = {
				requester_id: this.payload.requesterID,
				approver_id: await DataCenter.getData('employeeNo') || '',
				authorization_no: this.payload.authorizationNo ? this.payload.authorizationNo : 0,
				authorization_seq: this.payload.currentEmpApprovingSeqNo ? this.payload.currentEmpApprovingSeqNo : 0,
				authorization_status_code: this.payload.finalAuthorizerYN === 'Y' ? authCode.final : authCode.processing,
				authorization_remark: this.content ? this.content : ''
			}
			loading.openloading()
			return new Promise((resolve) => {
				window.bizMOB.Network.requestTr({
					_bMock: false,
					_sTrcode: this.payload.serviceID,
					_oApproval: {
						requester_id: authorizationBody.requester_id,
						approver_id: authorizationBody.approver_id,
						authorization_no: authorizationBody.authorization_no,
						authorization_seq: authorizationBody.authorization_seq,
						authorization_status_code: authorizationBody.authorization_status_code,
						authorization_remark: authorizationBody.authorization_remark,
					},
					_oBody: {
					...reqBody
					},
					_fCallback: (res: any, httpStatus: HttpHeader) => {
						loading.closeLoading()
						// error handling
						RequestErrorHandlingService.requestErrorHandler(res, httpStatus)

						Auth.initSession()
						resolve(Object.assign({}, res));
					}
				});
			});
		},
		onCancel(){
			this.$router.back()
		},
		async checkAllowOperation() {

			this.operation.allowApprove = this.payload.processableYN == 'Y'
			
			this.operation.allowCancel = this.payload.cancelableYN == 'Y'
		}
	}
})

interface Payload {
	authorizationNo : number,
	linkScreenNo : string,
	requestContent : string,
	currentEmpApprovingSeqNo : number,
	serviceID : string,
	finalAuthorizerYN : 'Y' | 'N',
	requesterID: string,
	processableYN: 'Y' | 'N',
	cancelableYN: 'Y' | 'N'
}
