import { Options, Input } from './format-input.type'

export default class ExpiryDateFormat {
    options: Options
    input: Input
    numberRegExp: RegExp

    constructor(config: Options) {
        this.options = Object.assign(config)
        this.input = ''        
        this.numberRegExp = new RegExp('[^0-9]+', 'gi')
    }

    isNull() {
        return !this.input?.toString().replace(this.numberRegExp, '')
    }

    numbers() {
        if (typeof this.input === 'number') {
            return this.input.toString().replace('-', '')
        } else {
            return this.input?.toString().replace(this.numberRegExp, '')
        }
    }

    format(input: Input): string {
        this.input = input
        if (this.isNull()) return "";
        const { separator } = this.options
        let inputValue = this.numbers()
        if (inputValue.length > 4) {
            inputValue = inputValue.substring(0, 4)
        }
        let replaceVal = inputValue.replace(/[^\dA-Z]/g, '').replace(/(.{2})/g, `$1${separator}`);
        // Remove the last '/'
        if (replaceVal.length === 6) {
            replaceVal = replaceVal.slice(0, -1) + '';
        }
        return replaceVal
    }
}