import Vuex from 'vuex'
const menuListData = new Vuex.Store({
    state: {
      menuList: []
    },
    mutations: {
      SET_MENU_LIST(state, menuList) {
        state.menuList = menuList
      }
    },
    actions: {
      setMenuList({ commit }, menuList) {
        commit('SET_MENU_LIST', menuList)
      }
    },
    getters: {
      getMenuData: state => state.menuList
    }
  }) as any
export default menuListData