import { MenuInfo_SideMenu } from "../type/Menu.type";

export class MenuService {

  public convertMenuListToRecursiveMenuList(menuList: MenuInfo_SideMenu[]): MappedMenuList[] {
    return this.mapMenuListToRecursiveMenuList(menuList)
  }

  private mapMenuListToRecursiveMenuList(array: MenuInfo_SideMenu [], parentID = "") {
    const result = [] as MappedMenuList[];
  
    for (const item of array) {
      if (item.parentMenuID.replace(/\s/g, "") === parentID.replace(/\s/g, "")) {
        const node = {
          ...item,
          screenPath: item.screenID.replace(/\s/g, ""),
          subMenu: this.mapMenuListToRecursiveMenuList(array, item.menuID),
        };
        result.push(node);
      }
    }
  
    return result;
  }
}

interface MappedMenuList {
  menuID: string,
  menuName: string,
  menuLevel: string,
  screenID: string,
  screenName: string,
  screenPath: string,
  parentMenuID: string, 
  webPageURL: string,
  subMenu: MappedMenuList []
}