import { Options, Input } from './format-input.type'
import DataformatService from '@/shared/services/DataFormatService'

const formatService = new DataformatService();

export default class AccountFormat {
    options: Options
    input: Input
    numberRegExp: RegExp

    constructor(config: Options) {
        this.options = Object.assign(config)
        this.input = ''
        this.numberRegExp = new RegExp('[^0-9]+', 'gi')
    }

    isNull() {
        return !this.input?.toString().replace(this.numberRegExp, '')
    }

    numbers() {
        if (typeof this.input === 'number') {
            return this.input.toString().replace('-', '')
        } else {
            return this.input?.toString().replace(this.numberRegExp, '')
        }
    }

    format(input: Input): string {
        this.input = input
        if (this.isNull()) return "";
        // Limit length 16, prevent when user copy & paste
        let inputValue = this.numbers()
        if(inputValue.length > 16) {
            inputValue = inputValue.substring(0, 16)
        }
        
        return formatService.formatAccountNumber(inputValue, this.options.separator)
    }

    unformat(input: Input): string {
        this.input = input
        if (this.isNull()) return "";
        const unformated = formatService.unFormatAccountNumber(this.numbers())
        return unformated
    }
}