import moment from "moment";
import LoggerService from "../bizMOB/LoggerService";
import { CARD_SCHEME_TYPE_CODE, CARD_STATUS_CODE, CARD_TYPE_FEATURE_CODE } from "../constants/common.const";
import type { currencyCode } from "../type/common.type";

const logger = new LoggerService();
class DataformatService {
    VISARegExp: RegExp
    MasterRegExp: RegExp
    UnionPayRegExp: RegExp
    JCBRegExp: RegExp

    constructor() {
        this.VISARegExp = /^(?:4[0-9]{12}(?:[0-9]{3})?)$/; // Start from 4, length 13 or 16 digits
        this.MasterRegExp = /^(?:5[1-5][0-9]{14})$/; // Start with 51 through 55, length 16 digits
        this.UnionPayRegExp = /^(62[0-9]{14,17})$/; //
        this.JCBRegExp = /^(?:(?:2131|1800|35\d{3})\d{11})$/; // Start with 2131 0r 1800, length 15 digits or starting with 35, length 16 digits
    }

    formatAccountNumber(accountNumber: string, separatorSymbol = "-") {
        // console.log('accountNumber', accountNumber, accountNumber.length)
        if (!accountNumber) return accountNumber

        if (accountNumber.length === 16) {
            if (accountNumber.substring(3, 4) === "0") { // Old Account Number
                accountNumber = accountNumber.replace(/(0000)([0-9]{3})([0-9]{2})([0-9]{6})([0-9]{1})/, `$2${separatorSymbol}$3${separatorSymbol}$4${separatorSymbol}$5`);
            } else { // New Account Number and Virtaul Account
                accountNumber = accountNumber.replace(/(000)([0-9]{1})([0-9]{3})([0-9]{8})([0-9]{1})/, `$2${separatorSymbol}$3${separatorSymbol}$4${separatorSymbol}$5`);
            }
        } else if (accountNumber.length === 13) {
            if (accountNumber.substring(0, 1) === "0") { // Old Account Number
                accountNumber = accountNumber.replace(/(0)([0-9]{3})([0-9]{2})([0-9]{6})([0-9]{1})/, `$2${separatorSymbol}$3${separatorSymbol}$4${separatorSymbol}$5`);
            } else { // New Account Number and Virtaul Account
                accountNumber = accountNumber.replace(/([0-9]{1})([0-9]{3})([0-9]{8})([0-9]{1})/, `$1${separatorSymbol}$2${separatorSymbol}$3${separatorSymbol}$4`);
            }
        } else if (accountNumber.length === 12) { // Old Account Number
            accountNumber = accountNumber.replace(/([0-9]{3})([0-9]{2})([0-9]{6})([0-9]{1})/, `$1${separatorSymbol}$2${separatorSymbol}$3${separatorSymbol}$4`);
        }
        return accountNumber;
    }

    unFormatAccountNumber(accountNumber: string): string {
        accountNumber = accountNumber.replace(/\D+/g, "");
        return accountNumber;
    }

    checkCardNumberMatchType(value: string): string {
        const numRegExp = new RegExp('[^0-9]+', 'gi')
        const valueNumOnly = value?.toString().replace(numRegExp, '').trim()
        if (!valueNumOnly.match(this.VISARegExp)
            && !valueNumOnly.match(this.MasterRegExp)
            && !valueNumOnly.match(this.UnionPayRegExp)
            && !valueNumOnly.match(this.JCBRegExp)) {
            return 'Card Number (Unsupported)'
        }
        return ''
    }

    formatCardNumber(value: string): string {
        const preSurRegExp = new RegExp(`.{1,4}`, 'g')

        if (value.match(this.VISARegExp) || value.match(this.MasterRegExp) || value.match(this.UnionPayRegExp) || value.match(this.JCBRegExp)) {
            const splitDigit = value.match(preSurRegExp)
            return splitDigit?.toString().replaceAll(',', ' ') || '';
        }
        return value
    }

    /**
     * MASKING FORMAT
     * @param value (string)
     * @param startMask (number)
     * @param endMask (number)
     * @param separateDigit (number, default = 4) digit that you want to add separator. EX: digit = 5 => result = 00000 00000 00000
     * @param maskSymbol (string, default = '*') symbol that you want to add instead number
    */
    formatMaskValue(
        value: string,
        startMask: number,
        endMask: number,
        separateDigit = 4,
        maskSymbol = '*'
    ): string {

        if (!value || startMask < 0 || startMask >= value.length || endMask < 0 || endMask > value.length || startMask >= endMask) {
            logger.error(' Masking with invalid value or startMask & endMask')
            return value
        }

        const maskLength = endMask - startMask;
        let stringValue = ''
        if (separateDigit > 0) {
            const preSurRegExp = new RegExp(`.{1,${separateDigit}}`, 'g')
            const maskedStr = value.substring(0, startMask) + maskSymbol.repeat(maskLength) + value.substring(endMask);
            const separateStringArr = maskedStr.match(preSurRegExp)
            stringValue = separateStringArr?.toString()?.replaceAll(',', ' ') || '';
        } else {
            stringValue = value.substring(0, startMask) + maskSymbol.repeat(maskLength) + value.substring(endMask);
        }
        return stringValue
    }

    formatCardSchemeType(value: string): string {
        if (value === CARD_SCHEME_TYPE_CODE.VISA) {
            return 'VISA';
        } else if (value === CARD_SCHEME_TYPE_CODE.MASTER) {
            return 'Master';
        } else if (value === CARD_SCHEME_TYPE_CODE.CSS) {
            return 'CSS (NBC Local Card)';
        } else if (value === CARD_SCHEME_TYPE_CODE.DINERS_CLUB) {
            return 'Diners Club';
        }
        return value;
    }

    formatCardStatus(value: string): string {
        if (CARD_STATUS_CODE.ACTIVE === value) {
            return 'Normal' // this.translate.instant('CARD_COMMON.LABEL.NORMAL'); // Normal
        } else if (CARD_STATUS_CODE.BLOCK === value) {
            return 'Block' //this.translate.instant('CARD_COMMON.LABEL.BLOCK'); // Block
        } else if (CARD_STATUS_CODE.INACTIVE === value) {
            return 'InActive' //this.translate.instant('CARD_COMMON.LABEL.INACTIVE'); //  InActive
        } else if (CARD_STATUS_CODE.DEACTIVATE === value) {
            return 'Deactive' // this.translate.instant('CARD_COMMON.LABEL.DEACTIVE'); //  Deactive
        } else if (CARD_STATUS_CODE.OVERDUE === value) {
            return 'Overdue' //this.translate.instant('CARD_COMMON.LABEL.OVERDUE'); //  Overdue
        }
        return value
    }

    formatCardType(value: string): string {
        if (CARD_TYPE_FEATURE_CODE.CREDIT === value) {
            return 'Credit' //this.translate.instant('CAR0110.MY_CARDS.CREDIT_CARD'); // 'Credit';
        }
        if (CARD_TYPE_FEATURE_CODE.DEBIT === value) {
            return 'Debit' // this.translate.instant('CAR0110.MY_CARDS.DEBIT_CARD'); // 'Debit';
        }
        if (CARD_TYPE_FEATURE_CODE.ATM === value) {
            return 'ATM' // this.translate.instant('CAR0110.MY_CARDS.DEBIT_CARD'); // 'ATM';
        }
        if (CARD_TYPE_FEATURE_CODE.ALL === value) {
            return 'All' // this.translate.instant('CAR0110.MY_CARDS.ALL'); // 'All';
        }
        return '';
    }

    formatPhoneNumber(phoneNumber: string): string {
        if (phoneNumber.length === 9) {
            phoneNumber = phoneNumber.replace(/([0-9]{3})([0-9]{3})([0-9]{3})/, '$1 $2 $3');
        } else if (phoneNumber.length === 10) {
            phoneNumber = phoneNumber.replace(/([0-9]{3})([0-9]{3})([0-9]{4})/, '$1 $2 $3');
        }
        return phoneNumber;
    }

    formatMoney(numbers: number, currency: string): string {
        let amount = '';
        const decimal = 2;
        if (currency === 'USD') {
            amount = amount + String(Number(numbers).toFixed(decimal).replace(/\B(?=(\d{3})+(?!\d))/g, ",")) + " " + currency;
        } else {
            amount = amount + (String(Math.round(numbers)).replace(/\B(?=(\d{3})+(?!\d))/g, ",")) + " " + currency;
        }

        return amount;
    }

    formatMoneyNoSymbol(numbers: number, currency: string) {
        let amount = '';
        const d = 2;
        if (currency === 'USD') {
            amount = amount + String(Number(numbers).toFixed(d).replace(/\B(?=(\d{3})+(?!\d))/g, ","));
        } else {
            amount = amount + (String(Math.round(numbers)).replace(/\B(?=(\d{3})+(?!\d))/g, ","));
        }
        return amount;
    }

    formatOnlyNumberAndCharactor(value: string): string {
        const s = value.replace(/[^a-zA-Z0-9]/g, "")
        return s;
    }

    // Format Date Time
    formatDate = (date: string, format = setUpDefault.formatDate) => {
        if (date) {
            return moment(date).format(format)
        }
        return ""
    }

    formatTime = (date: string, format = setUpDefault.formatTime) => {
        if (date) {
            return moment(date).format(format)
        }
        return ""
    }

    formatDateTime = (date: string, format = setUpDefault.formatDateTime) => {
        if (date) {
            return moment(date).format(format)
        }
        return ""
    }

    formatDateServer = (date: string, format = setUpDefault.formatDateServer) => {
        if (date) {
            return moment(date).format(format)
        }
        return ""
    }
    //Format Date Time

    formatCurrency(amount: number, currencyCode: currencyCode): string {
        if (currencyCode === "KHR") {
            return amount.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
        } else { //(currencyCode === "USD")
            const val = (+amount / 1).toFixed(2)
            return val.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
        }
    }
    unformatCurrency(formatedAmount: string) {
        if (formatedAmount) {
            const format = formatedAmount.split(".");
            const result = format[0].replace(/\D/g, "") + (format[1] != "00" && format[1] ? "." + format[1] : "");
            return result;
        }

    }

    onSort(item1, item2, key, formatDate, fomatTime) {
        if (!item1 || !item1 || !key) return

        if (typeof key === 'string' && key.toLowerCase().includes('date')) {
            const split = key.split('-')
            const fDate = formatDate !== "" ? formatDate : 'YYYYMMDD'
            const fTime = fomatTime !== "" ? fomatTime : 'hmmss'
            if (split.length == 1) {
                // Sort Time
                const datetime1 = item1[split[0]]
                const datetime2 = item2[split[0]]
                const format1 = moment(datetime1, fDate).format('DD-MMM-YYYY')
                const format2 = moment(datetime2, fDate).format('DD-MMM-YYYY')
                const date1 = new Date(format1) as any
                const date2 = new Date(format2) as any
                return date1 - date2
            } else if (split.length == 2) {
                // Sort Date and Time
                const datetime1 = item1[split[0]] + item1[split[1]]
                const datetime2 = item2[split[0]] + item2[split[1]]
                const format1 = moment(datetime1, `${fDate}${fTime}`).format('DD-MMM-YYYY, h:mm:ss')
                const format2 = moment(datetime2, `${fDate}${fTime}`).format('DD-MMM-YYYY, h:mm:ss')
                const date1 = new Date(format1) as any
                const date2 = new Date(format2) as any
                return date1 - date2
            }
        } else {
            return item1[key]?.toString().localeCompare(item2[key]?.toString())
        }
    }

    sortAscDesc(list, isAscending, key, formatDate = "", fomatTime = "") {
        if (!list) return list
        if (isAscending) {
            return list.sort((item1, item2) => this.onSort(item1, item2, key, formatDate, fomatTime))
        } else {
            return list.sort((item1, item2) => this.onSort(item2, item1, key, formatDate, fomatTime))
        }
    }

    iconSort(isAscending) {
        if (isAscending) {
            return ['descending', 'ico_sort']
        } else {
            return ['ascending', 'ico_sort']
        }
    }
}

export default DataformatService


export const setUpDefault = {
    formatDate: "DD/MM/YYYY",
    formatTime: "HH:mm:ss",
    formatDateTime: "DD/MM/YYYY HH:mm",
    formatDateTimeComa: "DD/MM/YYYY, HH:mm",
    formatDateServer: "YYYYMMDD"
}