import { Drivers, Storage } from '@ionic/storage';

const store = new Storage({
    name: '__WBStorage',
    driverOrder: [Drivers.IndexedDB, Drivers.LocalStorage],
    storeName: '_WBStore'
});

await store.create();

export abstract class DataCenter {
    public static async setData(key: string, value: any) {
        await store.set(key, window.btoa(JSON.stringify(value)))
        console.log('Data set');
    }

    public static async getData(key: string) : Promise<any> {
        return await store.get(key).then(v => {
            return v ? JSON.parse(window.atob(v)) : ''
        })
    }

    public static async removeData(key: string) {
        await store.remove(key)
        console.log('Data Removed');
    }

    public static async clearData() {
        await store.clear()
        console.log('Storage Cleared');
    }
}