import APM200001SP01 from "@/views/apm/apm200001sp01/apm200001sp01.vue";
import { modalController } from "@ionic/vue";
import DialogService from "./DialogService";
import PopupService from "./PopupService";
import { APM20000111_RES, HttpHeader } from "../type/RequestService.type";
import { Auth } from "@/auth";
import { APM20200113_REQ_BODY, APM20200113_RES } from "../type/APM20200113.type";
import { DataCenter } from "./DataCenter";
import router from "@/router";
import { Util } from "../util/util";
import { RequestErrorHandlingService } from "./RequestErrorHandlingService";

const util = new Util()
const dialog = new DialogService();
const popup = new PopupService();
export class ApprovalService {

  public async checkApprovalLine(trCode: string, isMock?: boolean, finalAuthorizerYN?: 'Y' | 'N') {
    if (this.preCheckServiceRequireApproval(trCode))
      if (!isMock) {
        if(await Auth.checkSessionTimeout()){
          Auth.limitApiRequest++
          Auth.handleSessionEnd()
        } else {
          if(finalAuthorizerYN != 'Y'){
            const employeeNo = await this.getEmployeeNo()
            return new Promise((resolve) => {
              window.bizMOB.Network.requestTr({
                _bMock:  false,
                _sTrcode: "APM20000111",
                _oBody: {
                  serviceID: trCode,
                  employeeNo: employeeNo
                },
                _fCallback: (res: APM20000111_RES, httpStatus: HttpHeader) => {
                  // error handling
                  RequestErrorHandlingService.requestErrorHandler(res, httpStatus)
                  Auth.initSession()
                  resolve(Object.assign({}, res));
                }
              });
            });
          } else {
            return {body: {list: [], requiredAuthoriseLineYN: 'N'}};
          }
        }
      } else {
        return {body: {list: [], requiredAuthoriseLineYN: 'N'}};
      }
    else {
      return {body: {list: [], requiredAuthoriseLineYN: 'N'}};
    }
  }

  public async showApprovalPopup(authLineList: APM20000111_RES, payload: any, trCode: string, tabId?: string) {
    return new Promise((resolve) => {
      setTimeout( async () => {
        await popup.onOpen({
          dataProp: {
            approvalList: authLineList,
            payload: payload,
            trCode: trCode,
            tabId: tabId
          },
          component: APM200001SP01,
          callback: async (v) => {
            if(v.header.result){
              setTimeout( async () => {
                await dialog.onOpen({
                  content: "Approval Requested",
                  dialogMode: 'Success',
                  callback: async () => {
                    await modalController.dismiss()
                  }
                });
              }, 500)
            }
            resolve({header: {result: false}});
          },
        });
      }, 500 )
    })
  }

  private async getEmployeeNo() : Promise<string> {
    return await DataCenter.getData('employeeNo') || ''; // For Testing Purpose Only
  }

  /**
   * PreCheck Service Require Approval
   * @param {string} serviceId Service ID to check
   * @returns {boolean}
   * 
   * Based on business, Inquiry doesn't require approval.
   * if service id's second from last char is 1, will return false as it is an inquiry service.
   */
  private preCheckServiceRequireApproval(serviceId: string) : boolean {
    const char = serviceId.charAt(serviceId.length - 2)
    return char !== '1'
  }

  /**
   * Get Authorization Detail
   * @returns {Promise<APM20200113_RES>} a promise of authorization detail
   * Request registered payload based on authorization no and employee no
   */
  public async getAuthorizationDetail() : Promise<APM20200113_RES> {
    const approverId = await this.getEmployeeNo()
    const body: APM20200113_REQ_BODY = {
      authorizationNo: Number(`${router.currentRoute.value.query.authorizationNo}`),
      employeeNo: await DataCenter.getData('employeeNo')
    };

    return new Promise((resolve) => {
      window.bizMOB.Network.requestTr({
        _bMock: false,
        _sTrcode: "APM20200113",
        _oApproval: {
          requester_id: `${router.currentRoute.value.query.employeeNo}`,
          approver_id: approverId,
          authorization_no: body.authorizationNo ? body.authorizationNo : 0,
          authorization_seq: 0,
          authorization_status_code: '',
          authorization_remark: '',
        },
        _oBody: {
          ...body
        },
        _fCallback: (res: APM20200113_RES, httpStatus: HttpHeader) => {
          // error handling
          RequestErrorHandlingService.requestErrorHandler(res, httpStatus)
          Auth.initSession()
          switch (util.checkServiceIdType(res.body.serviceID)) {
            case 'register':
              this.pushQuery('register')
              break;
            case 'update':
              this.pushQuery('edit')
              break;
          }
          resolve(Object.assign({}, res));
        }
      });
    });
  }

   /**
   * Append type query upon call
   * @param {string} type value of type to append to query
   * 
   * When call will append a query with type as key and value from arg
   * This will not refresh the current route.
   */
  private pushQuery(type: string){
    const query = { ...router.currentRoute.value.query, type: type }
    router.replace({query})
  }
}