import { createRouter, createWebHistory } from "@ionic/vue-router";
import { RouteRecordRaw } from "vue-router";
import RootRouter from "./RootRouter"
import CibRouter from "./CibRouter"
import ComRouter from "./ComRouter"
import DcmRouter from "./DcmRouter"
import PfmRouter from "./PfmRouter"
import PumRouter from "./PumRouter"
import RptRouter from "./RptRouter"
import ApmRouter from "./ApmRouter"
import IcmRouter from "./IcmRouter"
import SecRouter from "./SecRouter"
import DashboardLayout from "../layouts/Dashboard/DashboardLayout.vue";
import { DataCenter } from "@/shared/services/DataCenter";
/**
 * you can add router class to concat method
 * **/

// To add more main router
const routerObject = [
  CibRouter,
  ComRouter,
  DcmRouter,
  PfmRouter,
  PumRouter,
  RptRouter,
  RootRouter,
  ApmRouter,
  IcmRouter,
  SecRouter,

  // Dynamic Routing
  {
    path: '/:dynamic',
    name: 'DynamicRouting',
    component: DashboardLayout,
    children: [
      {
        path: '',
        name: 'DynamicComponent',
        component: () =>  import("../layouts/DynamicScreen/DynamicScreen.vue"),
      }
    ],
    props: true
  },
];

const routes: Array<RouteRecordRaw> = ([] as Array<RouteRecordRaw>).concat(
  ...routerObject
);
const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
});

/**
 *   
 * to:  url
 * from:  url
 * next: to  url    
 */
// router.beforeEach((to, from, next) => {
//   // Backbutton Default Event Setup
//   window.bizMOB.setEvent("backbutton", () => {
//     router.back();
//   });

//   //  path   path   (Ex.       -- next("/login"))
//   next();
// });

router.beforeEach(async (to) => {
  const isAuth = await DataCenter.getData('isAuthenticated')
  if (!isAuth && to.name !== 'sign-in' ) {
    // redirect the user to the login page
    return { name: 'sign-in' }
  }
})

export default router;
