export class FormValidation {
    minMaxValueValidation(min: number, max: number): boolean {
        let result: boolean;
        if (min > max) {
            result = false;
        } else {
            result = true;
        }
        return result;
    }
    startDateEndDateValidation(startDate: Date, endDate: Date): boolean {
        let result: boolean;
        if (startDate > endDate) {
            result = false;
        } else {
            result = true;
        }
        return result;
    }
    validateEmail(email: string): boolean {
        const regEx = new RegExp(/^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|.(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/)

        return regEx.test(email);
    }
    validateWhiteSpace(value: string): boolean {
        const regEx = new RegExp(/^\s+|\s+$|\s+(?=\s)/g, "");

        return regEx.test(value);
    }
    validateSpace(event: any): void {
        if(/\s/.test(event.key)) return event.preventDefault()
    }
    validateNumber(event): void {
        const BACKSPACE = 8;
        const LEFT_ARROW = 37;
        const RIGHT_ARROW = 39;
        const DELETE = 46;
        const ENTER = 13;
        const DECIMAL = 190;
        const keyEvent = event
        const charCode = (keyEvent.which) ? keyEvent.which : keyEvent.keyCode;
        if ((!(/\d/.test(event.key)) // 0-9
          && (charCode !== BACKSPACE)
          && (charCode !== LEFT_ARROW)
          && (charCode !== RIGHT_ARROW)
          && (charCode !== DELETE)
          && (charCode !== ENTER)
          && (charCode !== DECIMAL)) 
          && (charCode !== 65 && event.ctrlKey !== true) 
          && (charCode !== 67 && event.ctrlKey !== true) 
          && (charCode !== 88 && event.ctrlKey !== true)
        ){ 
          return keyEvent.preventDefault()
        }
    }

    /**
     * check validate input for Latitude in Google Map
     * @param value Input value
     * @returns True or False
     */
    validateLatitude(value: string): boolean {
        const latReg = new RegExp("^-?([0-9]{1,2}|1[0-7][0-9]|180)([0-9]{1,2}|1[0-7][0-9]|180)(\\+|-)?((\\d((\\.)|\\.\\d{1,6})?)|(0*?[0-8]\\d((\\.)|\\.\\d{1,6})?)|(0*?90((\\.)|\\.0{1,6})?))")

        return latReg.test(value) || value.toString().split('.')[0].length < 1 || value.toString().split('.')[0].length > 2
    }

    /**
     * check validate input for Longitude in Google Map
     * @param value Input value
     * @returns True or False
     */
    validateLongitude(value: string): boolean {
        const longReg = new RegExp("^-?([0-9]{1,2}|1[0-7][0-9]|180)([0-9]{1,2}|1[0-7][0-9]|180)([0-9]{1,2}|1[0-7][0-9]|180)(\\+|-)?((\\d((\\.)|\\.\\d{1,6})?)|(0*?[0-8]\\d((\\.)|\\.\\d{1,6})?)|(0*?90((\\.)|\\.0{1,6})?))$")
        
        return longReg.test(value) || value.toString().split('.')[0].length < 1 || value.toString().split('.')[0].length > 3
    }
    
    /**
     *  Not allow to type any letter or number
     * @param event use with @keydown event
     * @returns
     */
    keyDownLatitudeAndLongitude(event): void {
        const BACKSPACE = 8;
        const LEFT_ARROW = 37;
        const RIGHT_ARROW = 39;
        const DELETE = 46;
        const ENTER = 13;
        const DECIMAL = 190;
        const charCode = (event.which) ? event.which : event.keyCode;
        if((event.target.value.length >= 12)
          && charCode !== 8 // 0-9
          && (charCode !== BACKSPACE)
          && (charCode !== LEFT_ARROW)
          && (charCode !== RIGHT_ARROW)
          && (charCode !== DELETE)
          && (charCode !== ENTER)
          && (charCode !== DECIMAL) 
          && (charCode !== 65 && event.ctrlKey !== true) 
          && (charCode !== 67 && event.ctrlKey !== true) 
          && (charCode !== 88 && event.ctrlKey !== true)
        ){
          return event.preventDefault()
        }
    }

    /**
     * 
     * @param contentHtml 
     * @returns convert HTML to String
     */
    formatHtmlToString(contentHtml: string): string | any {
        try {
            return contentHtml
            .replace(/\n/gi, "")
            .replace(/<style([\s\S]*?)<\/style>/gi, "")
            .replace(/<script([\s\S]*?)<\/script>/gi, "")
            .replace(/<a.*?href="(.*?)[?"].*?>(.*?)<\/a.*?>/gi, " $2 $1 ")
            .replace(/<\/div>/gi, "\n\n")
            .replace(/<\/li>/gi, "\n")
            .replace(/<li.*?>/gi, "  *  ")
            .replace(/<\/ul>/gi, "\n\n")
            .replace(/<\/p>/gi, "\n\n")
            .replace(/<br\s*?>/gi, "\n")
            .replace(/<[^>]+>/gi, "")
            .replace(/^\s*/gim, "")
            .replace(/ ,/gi, ",")
            .replace(/ +/gi, " ")
            .replace(/\n+/gi, "\n\n")
            .replace(/&nbsp;/gi, "")
            .replace(/&amp;/gi, "&")
            .replace(/&lt;/gi, "<")
            .replace(/&gt;/gi, ">")
            .replace(/&quot;/gi, `${""}`)
            .replace(/&apos;/gi, "'")
            .replace(/&cent;/gi, "")
            .replace(/&pound;/gi, "")
            .replace(/&yen;/gi, "")
            .replace(/&euro;/gi, "")
            .replace(/&copy;/gi, "")
            .replace(/&reg;/gi, "")

        } catch {
            console.error()
        }
    }
}