import type { Code } from "../type/common.type";
import { CommonCodeType } from "../type/commonCodeType";
import { StandardCodeDomainName } from "../type/commonCodeType";
import store from "@/store";
export class ConvertCode {
    public convertValue(type: Code | StandardCodeDomainName, code: string, mode?: 'code' | 'value'): string {
        const { CommonCodeModule }: any = store.state;
        let result = "";
        if (CommonCodeModule.commonCode[type] !== undefined || "") {
            switch (mode) {
                case 'value':
                    CommonCodeModule.commonCode[type].filter((value: CommonCodeType) => {
                        if (code === value.value) {
                            result = value.key;
                        } else {
                            result;
                        }
                    });
                    break;
                default:
                    CommonCodeModule.commonCode[type].filter((value: CommonCodeType) => {
                        if (code === value.key) {
                            result = value.value;
                        } else {
                            result;
                        }
                    });
                    break;
            }
        } else {
            result;
        }
        return result;
    }
}
