import DataFormatService from "../services/DataFormatService";
import { ConvertCode } from "../services/ConvertCodeService";
import type { Code } from "../type/common.type";

const dataFormat = new DataFormatService();
const codeConverterService = new ConvertCode()

const myPlugin = {
    install(app) {
        app.config.globalProperties.$codeConverter = (type: Code, code: string) => {
            if (type) {
                return codeConverterService.convertValue(type, code);
            }
            return type;
        }
        app.config.globalProperties.$accountFormat = (value: string, separatorSymbol?: string) => {
            if ( value ) { //&& args[0] === '000'
                return dataFormat.formatAccountNumber( value, separatorSymbol );
            }
            return value;
        }
        app.config.globalProperties.$unformatAccount = (value: string) => {
            if ( value ) {  //&& args[0] === '000'
                return dataFormat.unFormatAccountNumber( value );
            }
            return value;
        },
        app.config.globalProperties.$formatMaskValue = (value: string, startMask: number, endMask: number, separateDigit?: number, maskSymbol?: string) => {
            if ( value ) {
                return dataFormat.formatMaskValue( value, startMask, endMask, separateDigit, maskSymbol );
            }
            return value;
        }
        app.config.globalProperties.$formatCardSchemeType = (value: string) => {
            if ( value ) {
                return dataFormat.formatCardSchemeType( value );
            }
            return value;
        }
        app.config.globalProperties.$formatCardStatus = (value: string) => {
            if ( value ) {
                return dataFormat.formatCardStatus( value );
            }
            return value;
        }
        app.config.globalProperties.$formatCardType = (value: string) => {
            if ( value ) {
                return dataFormat.formatCardType( value );
            }
            return value;
        }
        app.config.globalProperties.$formatCardNumber = (value: string) => {
            if ( value ) {
                return dataFormat.formatCardNumber( value );
            }
            return value;
        }
        app.config.globalProperties.$checkCardNumberMatchType = (value: string) => {
            if ( value ) {
                return dataFormat.checkCardNumberMatchType( value );
            }
            return value;
        }
    }
}
 
export default myPlugin;