import { Options, Input } from './format-input.type'
export default class CurrencyFormat {
    options: Options
    number: Input
    input: Input
    numberRegExp: RegExp
    isBlur : boolean
    preSurRegExp : RegExp
    positiveNumRegExp : RegExp
    negativeRegExp : RegExp

    constructor(config: Options) {
        this.options = Object.assign(config)
        this.input = ''
        this.number = ''
        this.isBlur = false

        const { prefix, suffix, decimal } = this.options

        this.preSurRegExp = new RegExp(`${prefix}|${suffix}`, 'g')
        this.positiveNumRegExp = new RegExp('[^0-9]+', 'gi')
        this.negativeRegExp = new RegExp('[^0-9\\-]+', 'gi')
        this.numberRegExp = new RegExp(`[^0-9\\${decimal}]+`, 'gi')
    }

    blur(blur = false) {
        this.isBlur = blur
        return this
    }

    numberOnly(str: Input, regExp: RegExp) {
        return str?.toString().replace(regExp, '')
    }

    isNull() {
        if (this.isBlur) {
            return !this.numberOnly(this.input, this.positiveNumRegExp)
        }
        return !this.numberOnly(this.input, this.negativeRegExp)
    }

    numbers() {
        const { fractionDigits, decimal } = this.options
        const input = this.input.toString().replace(this.preSurRegExp, '')
        this.number = this.numberOnly(input, this.numberRegExp)

        if (!this.number.includes(decimal) && this.isBlur) {
            this.number = fractionDigits !== 0 ? this.number + decimal + '0'.repeat(fractionDigits) : this.number
        }
        
        return this.number
    }

    sign() {
        if (this.input === null || this.input === undefined) {
            return ''
        }
        const hasMinus = this.input.toString().indexOf('-') >= 0
        if (this.isBlur) {
            // return hasMinus && this.realNumber() > 0 ? '-' : ''
        }
        return hasMinus ? '-' : ''
    }

    isNegative() {
        return this.sign() === '-'
    }

    realNumber() {
        const { separator, fractionDigits } = this.options
        let rmSeparator = this.input.toString().replaceAll(separator, '')
        const zeroAfterDigit = '.' + '0'.repeat(fractionDigits)

        if (rmSeparator.includes(zeroAfterDigit)) {
            rmSeparator = rmSeparator.replace(zeroAfterDigit, '')
        }

        return rmSeparator
    }

    addSeparator() {
        const { decimal, separator } = this.options
        const parts: Input[] = this.numbers().split(decimal)
        parts[0] = parts[0].toString().replace(/(\d)(?=(?:\d{3})+\b)/gm, `$1${separator}`)
        return parts.join(decimal)
    }

    format(input: Input): string {
        this.input = input
        if (this.isNull()) return "";

        const formated = this.options.prefix + this.sign() + this.addSeparator() + this.options.suffix
            
        return formated
    }

    unformat(input: Input): string {
        this.input = input
        if (this.isNull()) return ""
        return this.sign() + this.realNumber()
    }
}