
import { defineComponent } from 'vue';
import { MenuInfo_SideMenu } from '@/shared/type/Menu.type';
import { Header } from '@/shared/type/service-header';
import { MenuService } from '@/shared/services/MenuService';
import { modalController } from '@ionic/vue';
import menuListData from '@/store/modules/menuListData';
import { DataCenter } from '@/shared/services/DataCenter';
import { NotificationService } from '@/shared/services/NotificationService';
import { Auth } from '@/auth';
const menuService = new MenuService()

export default defineComponent({
  name: 'DashboardLayout',
  data() {
    return {
      isSidebarCollapsed: false,
      menuList: [] as MenuInfo_SideMenu[],
      userInfo: {
        employeeNo: '',
        employeeName: '',
        branchName: '',
        branchCode: '',
      },
      notificationCount: 0,
      windowWidth: 0
    }
  },

  created() {
    this.getMenuList()
    this.getUserInfo()
    
  },

  async mounted(){
    this.handleResize()
    await DataCenter.getData('sidemenu_state').then(v => {
      this.isSidebarCollapsed = v
    }).then(()=> {
      window.addEventListener('resize', this.handleResize)
    })

    //listen to notification count
    window.addEventListener('notification_update', (event: any) => {
      this.notificationCount = event.detail.notification_count
      if(this.notificationCount > 0) {
        NotificationService.endNotificationInquiryInterval()
      }
    })
  },

  unmounted(){
    window.removeEventListener('resize', this.handleResize)
  },

  computed: {
    profileColor(): string {
      const firstChar = this.userInfo.employeeName.charAt(0).toLocaleUpperCase()

      switch (true) {
        case /[A-G]/.test(firstChar):
          return "bg01"
        case /[H-N]/.test(firstChar):
          return "bg02"
        case /[O-T]/.test(firstChar):
          return "bg03"
        case /[U-Z]/.test(firstChar):
          return "bg04"
        default:
          return "bg01"
      }
    }
  },

  methods: {
    handleResize(){
      if(window.innerWidth < 1366){
        this.isSidebarCollapsed = true
      } else {
        this.isSidebarCollapsed = false
      }
    },
    onCloseSubmenu(e) {
      if(!document.getElementById('sidebar')?.contains(e.target) && this.isSidebarCollapsed){
        const children = document.getElementById('accordionGroup')?.children
        if(children) {
          for (let index = 0; index < children?.length; index++) {
            children[index].classList.remove('accordion-expanded')
            children[index].classList.add('accordion-collapsed')
          }
        }
      }
    },

    async getMenuList() {
      const reqBody : COM20400112_REQ_BODY = {
        pageNumber: 0,
        pageSize: 10000
      }

      const resData = await this.$request.getRequest('COM20400112', reqBody, false) as COM20400112_RES
      if(resData.header.result){
        this.menuList = menuService.convertMenuListToRecursiveMenuList(resData.body?.list)[0].subMenu
        menuListData.dispatch('setMenuList',resData.body.list)
      }
    },

    onClickLogout() {
      this.$dialog.onOpen({
        content: "Are you sure you want to logout?",
        dialogMode: 'Question',
        callback : async () => {
            await Auth.onLogout()
            await modalController.dismiss()
        }
      })
    },

    onClickMenu(subMenu: [], path: string){
      if(subMenu.length == 0){
        if(path){
          this.$router.push(`/${path}`)
        } else {
          this.$router.push(`/error4`)
        }
      }
    },

    handleRouterPush(menu){
      if(menu.screenPath){
        this.$router.push(`/${menu.screenPath}`)
      } else if(menu.webPageURL){
        window.open(`${menu.webPageURL}`, '_blank')
      } else {
        this.$router.push('/error4')
      }
    },

    async handleCollapseSidebar() {
      this.isSidebarCollapsed = !this.isSidebarCollapsed
      await DataCenter.setData('sidemenu_state', this.isSidebarCollapsed)
    },

    handleCheckActive(routePath: string, screenPath: string, index: string) : boolean {
      if(routePath.slice(1, 10) == screenPath.slice(0,9)){
        this.toggleAccordion(index)
        return true
      } else {
        return false
      }
    },

    toggleAccordion(index: string){
      if(!this.$refs.accordionGroup){
        return
      }
      const nativeEl = (this.$refs.accordionGroup as any).$el

      if (nativeEl.value === index) {
          nativeEl.value = undefined;
        } else {
          nativeEl.value = index;
        }
    },

    onClickNotification(){
      this.$router.push('/PUM202002SC00')
    },

    onClickPushToHome(){
      this.$router.push(`COM200001SC00`)
    },

    async getUserInfo(){
      this.userInfo.branchCode = await DataCenter.getData('branchCode') ? await DataCenter.getData('branchCode') as string : '001'
      this.userInfo.branchName = await DataCenter.getData('branchName') ? await DataCenter.getData('branchName') as string : 'TempBranch'
      this.userInfo.employeeNo = await DataCenter.getData('employeeNo') ? await DataCenter.getData('employeeNo') as string : '0001'
      this.userInfo.employeeName = await DataCenter.getData('employeeName') ? await DataCenter.getData('employeeName') as string : 'TempUser'
    },
  }
})

//======= ONLY FOR SIDEMENU =======//

interface COM20400112_REQ_BODY {
  pageNumber
  pageSize
}

interface COM20400112_RES {
  header : Header
  body : {
    totalCount : number 
    list: MenuInfo_SideMenu []
  }
}
