import { COMMON_KEY_VALUE } from "@/shared/constants/common.const";
import { CommonCodeType } from "@/shared/type/commonCodeType";
import { Code } from "@/shared/type/common.type";
import { loadingService } from "@/shared/services/LoadingScreenService";
import { COM21100113_REQ_BODY, COM21100113_RES, COMMON_CODE } from "@/shared/type/COM21100113.type";
import { RequestErrorHandlingService } from "@/shared/services/RequestErrorHandlingService";
import { HttpHeader } from "@/shared/type/RequestService.type";

const loading = new loadingService();
interface CommonType {
	key: string;
	value: string;
}

type BindCommonCode = CommonCodeType | Code;

const state = {
	standardCodeDomainName: "" as string,
	commonCode: {} as BindCommonCode,
	value: "" as string,
}

const getters = {
	getCommonCode(state: any) {
		return state.commonCode;
	},
	getValue(state: any) {
		return state.value;
	}
}

const mutations = {
	setCommonCode(state: any, data: any) {
		state.commonCode = data;
	},
	setValue(state: any, data: string) {
		state.value = data;
	},
}

const actions = {
	async requestCommonCode({ commit }: any): Promise<any> {
		loading.openloading()
		return new Promise((resolve) => {
			const body: COM21100113_REQ_BODY = {
				standardCodeDomainName: "",
				standardCodeValue: "",
				languageCode: "",
				pageNumber: 1,
				pageSize: 10000,
			};

			window.bizMOB.Network.requestTr({
				_bMock: false,
				_sTrcode: "COM21100113",
				_oBody: {
					...body,
				},
				_fCallback: (res: COM21100113_RES, httpStatus: HttpHeader) => {
					loading.closeLoading()
					if (res.header.result) {
						const commonConvertCode: any = res.body.list.reduce(
							(previous: COMMON_CODE, current: COMMON_CODE) => {
								previous[current.standardCodeDomainName] = previous[current.standardCodeDomainName] || [];
								previous[current.standardCodeDomainName].push({
									key: current.standardCodeValue,
									value: current.standardCodeValueDesc,
								});
								return previous;
							},
							Object.create([])
						);
						const result = { ...COMMON_KEY_VALUE, ...commonConvertCode };

						resolve(commit("setCommonCode", result));
					} else {
						RequestErrorHandlingService.requestErrorHandler(res, httpStatus)
					}
				},
			});
		});
	},

	convertCodeToValue({ commit }: any, data: { domainName: string, code: string, mode?: 'code' | 'value' }) {
		if (state.commonCode[data.domainName] !== undefined || "") {
			state.commonCode[data.domainName].filter((item: CommonType) => {
				switch (data.mode) {
					case 'value':
						if (data.code === item.value) {
							commit("setValue", item.key);
						}
						break;
					default:
						if (data.code === item.key) {
							commit("setValue", item.value);
						}
						break;
				}
			})
		} else {
			commit("setValue", "")
		}
	}
}


export default {
	namespaced: true,
	state,
	getters,
	actions,
	mutations
}
