export class Util {
    /**
     * Focus on Table Row
     * @param {string} rowId Row ID within a table
     * 
     * A function used to apply or remove 'focus' style class to child of 'tbody_section'
     * style class base on rowId upon call
     */
    public rowFocus(rowId: string) {
        try {
            const parentEl = document.getElementsByClassName('tbody_section')
            for (const element of parentEl[0].children) {
                const child = element
                if (child.id == rowId) {
                    child.classList.add('focus')
                } else {
                    child.classList.remove('focus')
                }
            }
        } catch (error) {
            console.log(error);
        }
    }

    /**
     * Get Cookie By Name
     * @param {string} name Name of cookie
     * @returns return cookie value base on its key 
     * 
     * A function used to get all cookie set by current domain and
     * return cookie value base on its key
     */
    public getCookie(name: string): string {
        const regex = new RegExp(`(^| )${name}=([^;]+)`)
        const match = RegExp(regex).exec(document.cookie)
        if (match) {
            return match[2]
        }
        return ''
    }

    /**
     * Add comma to number by index
     * @param {number} number number to add comma
     * @param {number} index index on the number to add comma
     * @returns return a string of the original value splitted by comma
     * 
     */
    public addComma(number: number, index: number): string {
        const str = number.toString().split('.');
        if (str[0].length >= index) {
            str[0] = str[0].replace(/(\d)(?=(\d{3})+$)/g, '$1,');
        }
        if (str[1] && str[1].length >= index) {
            str[1] = str[1].replace(/(\d{3})/g, '$1 ');
        }
        return str.join('.');
    }

    /**
     * Check the service id type
     * @param {string} serviceId service id
     * @returns string of the type of service id
     * 
     * A function used to check what type of service it is.
     * return register, update, list or delete
     */
    public checkServiceIdType(serviceId: string): 'register' | 'update' | 'list' | 'delete' {
        const char = serviceId.charAt(serviceId.length - 2)
        switch (char) {
            case '1':
                return 'list'
            case '2':
                return 'register'
            case '3':
                return 'update'
            case '4':
                return 'delete'
            default:
                return 'list'
        }
    }

    /**
     * Disable and enable input in Text Editor
     * @param {boolean} props
     * 
     * for enable/disable edit function in quill text editor
     */
    public editableContent(props: boolean) {
        const editableInput = document.querySelectorAll(".ql-editor") as NodeListOf<Element>
        const editableContent = document.querySelectorAll(".ql-container") as NodeListOf<Element>
        const disabledTool = document.querySelectorAll(".ql-toolbar") as NodeListOf<Element>
        for(let i = 0; i < editableInput.length; i++) { 
          editableInput[i]?.setAttribute("contenteditable", `${props}`)
          if(props === true) {
            disabledTool[i]?.removeAttribute("style")
            editableContent[i]?.classList.remove("ql-disabled")
          } else {
            disabledTool[i]?.setAttribute("style", "pointer-events: none")
            editableContent[i]?.classList.add("ql-disabled")
          }
        }
    }

    /**
     * Helper function for scroll to html element
     * @param {string} className css classname for function to scroll to
     * 
     * A function used to scroll to a specific position in the DOM base on css class.
     */
    public scrollToElement(className: string) {
        if (className) {
            const element = document.getElementsByClassName(className)
            if (element) {
                if(element.length > 0){
                    setTimeout(() => { 
                        try {
                            element[0].scrollIntoView({ block: 'center',  behavior: 'smooth' }) 
                        } catch (error) {
                            console.log(error);
                        }
                    }, 100)
                }
            }
        }
    }
}